@use 'base';

@mixin visiblity($color) {
    border: 1px solid $color;
}

@mixin BoxMargin {
    margin: 1rem 0 0.25rem 0;
}


.light {
    background-color: base.$white;
    color: base.$grey;

    .reverse {
        background-color: base.$grey;
        // color: base.$white;
    }

    .border {
        border: 0.3rem solid base.$grey;
    }
}

.dark {
    background-color: base.$black;
    color: base.$white2;

    .reverse {
        background-color: base.$white;
        // color: base.$white;
    }

    .border {
        border: 0.3rem solid base.$white2;
    }

}

.grey {
    background-color: base.$grey;
    color: base.$white;

    .reverse {
        background-color: base.$white;

    }

    .border {
        border: 0.3rem solid base.$white;
    }
}

body {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}

:root {
    display: block;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

@media (max-width:300px) {

    :root {
        display: none;

    }

}

.initial {
    display: block;
    width: 100vw;
    height: 100vh;
    overflow-wrap: break-word;
    overflow: scroll;
}

.header-div {
    display: flex;
    flex-direction: row;
    height: max-content;
    width: 100vw;
    justify-content: space-evenly;
    align-items: center;
    position: sticky;
    @include BoxMargin();

}


.links {
    display: flex;
    width: 100%;
    height: 3rem;
    text-decoration: none;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-family: base.$ff3;
    padding: 0.25rem 0 0.25rem 0;
}

.links:hover {
    text-decoration: underline !important;
}

.links:active {
    text-decoration: underline !important;
}

.toggle {
    display: flex;
    height: 3rem;
    width: 100%;
    align-self: center;
    justify-content: center;
    @include BoxMargin();

    svg {
        height: 2rem;
        width: 100%;
        align-self: center;
    }

}


.menu {
    display: none;
    position: absolute;
    top: 1rem;
    right: 1rem;
    width: 2rem;
    height: 2rem;
    flex-direction: column;
    justify-content: space-evenly;
    float: right;
    align-items: center;
}

.spanDiv {
    display: none;
    height: 0.25rem;
    width: 1.4rem;
    border-radius: 0.2rem;
}

@media (max-width:768px) {

    .header-div {
        display: flex;
        flex-direction: column;
    }

    .links.close {
        display: none;
    }

    .toggle.close {
        display: none;
    }

    .menu {
        display: flex;
        position: absolute;
    }

    .menu.opened {
        margin-bottom: 1rem;
    }

    .spanDiv {
        display: flex;
    }

}


main {
    @include BoxMargin();
    overflow-wrap: break-word;
    overflow: scroll;
}

.searchbar-div {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    width: 100%;
    margin: 2rem 0 1rem 0;


    form {
        grid-column: 2/3;
        justify-content: center;
    }

    select {
        display: none;
        grid-column: 2/3;
        justify-content: center;
    }

    option {
        z-index: 1;
    }
}

.titlePage{
    display: flex;
    flex-wrap: wrap;
    font-size: 1.5rem;
    height: max-content;
    justify-content: start;
    width: inherit;
    font-family: base.$ff2;
    margin:0 5rem 0 5rem;


    a {
        color: darkgreen;
       
    }

}
.paragraphs {
    display: flex;
    grid-template-columns: 0.5fr 1fr 0.5fr;
    flex-wrap: wrap;
    font-size: 1.5rem;
    height: 80vh;
    width: inherit;
    font-family: base.$ff2;


    a {
        color: darkgreen;
       
    }

    p {
        display: flex;
        grid-column: 2/3;
        justify-content: center;
        margin: 5rem;
        
    }
}


.projects {
    display: grid;
    height: max-content;
    width: 100vw;
    justify-content: center;
    font-size: 1.5rem;
    font-family: base.$ff2;

    .projectsContainer {
        display: grid;
        gap: 2rem;
        margin: 1rem 1rem 1rem 1rem;

        .items.projectItem {
            display: grid;
            grid-template-rows: 1fr 4fr 1fr 1fr 1.5fr;
            grid-template-columns: 4fr 2fr 2fr;
            grid-template-areas:
                'projectTitle projectTitle projectTitle'
                'images projectDescription projectDescription'
                'images demo source'
                'images demo source'
                'keywords keywords keywords';
            height: 30rem;
            gap: 0.5rem;
            align-content: center;
            min-width: 50vw;
            max-width: 88vw;

            .projectImg {
                display: flex;
                grid-area: images;
                border-radius: 1rem;
                padding: 1rem;

                // height: 15rem;
                // width: 11rem;
                flex-grow: 1;
                flex: 1;
                align-items: center;

            }

            .title {
                display: flex;
                grid-area: projectTitle;
                border-radius: 1rem;

                padding: 0.25rem;
                align-items: center;
                justify-content: center;

            }

            .description {
                display: flex;
                grid-area: projectDescription;
                border-radius: 1rem;

                overflow-wrap: break-word;
                overflow: scroll;
                text-align: justify;
                padding: 0.25rem;
            }

            .sourceUrl {
                display: flex;
                grid-area: source;
                border-radius: 1rem;
                align-items: center;
                justify-content: center;
                padding: 0.25rem;

                & :hover {
                    text-decoration: underline !important;

                }

                a {
                    text-decoration: none;
                    color: inherit;
                }
            }

            .demoUrl {
                display: flex;
                grid-area: demo;
                border-radius: 1rem;
                align-items: center;
                justify-content: center;
                padding: 0.25rem;

                & :hover {
                    text-decoration: underline !important;

                }

                a {
                    text-decoration: none;
                    color: inherit;
                }
            }

            .urlDescription {
                display: flex;
                grid-area: urlSubtext;
                border: none;
                font-size: 1rem;
            }




            .keywords {
                display: flex;
                grid-row-start: keywords;
                grid-row-end: sourceDescription;
                grid-column-start: keywords;
                grid-column-end: keywords;
                border-radius: 1rem;
                padding: 0.25rem;
                align-items: center;
                justify-content: start;


            }
        }
    }
}



@media screen and (max-width: 300px) {
    .items.projectItem {
        display: grid;
        grid-template-rows: 1fr 3fr 2fr 1fr 2fr;
        //grid-template-rows: [1] 1fr [2] 4fr [3] 1fr [4]0.5fr [5]1fr;
        grid-template-columns: 1fr;
        grid-template-areas:
            'projectTitle'
            'images'
            'description'
            'demoUrl'
            'sourceUrl'
            'keywords';
        height: 30rem;
        gap: 0.5rem;
        align-content: center;
        // min-width: 20vw;
        max-width: 100vw;

        .projectImg {
            display: flex;
            grid-area: images;
            border-radius: 1rem;
            padding: 1rem;
            // height: 15rem;
            // width: 11rem;
            flex-grow: 1;
            flex: 1;
            align-items: center;
            border: 10px solid yellow;
            background-color: pink;
        }

        .title {
            display: flex;
            grid-area: projectTitle;
            border-radius: 1rem;
            border: 3px solid blue;
            background-color: lightblue;
            padding: 1rem;
            align-items: center;
            justify-content: center;
        }

        .description {
            display: flex;
            grid-area: projectDescription;
            border-radius: 1rem;
            overflow-wrap: break-word;
            overflow: scroll;
            text-align: justify;
            padding: 0.8rem;
            border: 3px solid red;
            background-color: rgb(244, 180, 61);
        }

        .sourceUrl {
            display: flex;
            // grid-column-start:source;
            // grid-column-end:sourceDescription;
            grid-area: source;
            border-radius: 1rem;
            align-items: center;
            justify-content: center;
            border: 1px solid green;
            background-color: yellow;
        }

        .demoUrl {
            display: flex;
            min-width: 1fr;

            // grid-column-start:demo;
            // grid-column-end:demoDescription;

            grid-area: demo;
            border-radius: 1rem;
            align-items: center;
            justify-content: center;
            border: 1px solid yellow;
            background-color: grey;
        }

        .demoUrl:hover {
            content: 'something';
            display: block;
            grid-area: demoDescription;
            z-index: 1;
            //transition: ;



            text-decoration: underline !important;
            content: "sss";
        }

        .keywords {
            display: flex;
            grid-row-start: keywords;
            grid-row-end: sourceDescription;
            grid-column-start: keywords;
            grid-column-end: keywords;

            //grid-area: keywords;
            border-radius: 1rem;
            padding: 1rem;
            align-items: center;
            justify-content: start;
            border: 1px solid purple;
            background-color: yellowgreen;
        }
    }

}

;

.footer {
    display: grid;
    grid-row: 1fr;
    grid-template-columns: 0.25fr 1fr;
    height: 4rem;
    justify-self: center;
    align-items: center;
    font-size: 1rem;
    margin: 1rem 1rem 1rem 1rem;

    img {
        grid-column: 1/2;
        height: 2em;
        width: 2em;
        border-radius: 50%;
        justify-self: center;
    }

    p {
        grid-column: 2/3;
        justify-self: center;
    }
}

.bar {
    border: 0;
    width: 100%;
    height: 4px;
    background-color: inherit;
    position: relative;
    overflow: visible;
    background-color: navy;
}

.bar::after {
    content: '';
    display: block;
    width: 4.5rem;
    height: 2rem;
    position: absolute;
    right: 0;
    top: 0;
    background-image: linear-gradient(44deg, transparent 2rem, navy 1rem);
}